import * as THREE from 'three';
import { Atmosphere } from '../materials/Atmosphere.js';
import { noiseShader } from '../shaders/noise.js';

export class Planet {
    constructor(options = {}) {
        this.options = {
            radius: options.radius || 10,
            detail: options.detail || 32,
            color: options.color || new THREE.Color(0x44ff44),
            atmosphereColor: options.atmosphereColor || new THREE.Color(0x00b3ff),
            atmosphereScale: options.atmosphereScale || 1.15,
            cloudSpeed: options.cloudSpeed || 0.0005,
            rotationSpeed: options.rotationSpeed || 0.0001,
            name: options.name || "Unnamed Planet",
            type: options.type || "Terrestrial",
            resources: options.resources || {
                minerals: Math.random() * 100,
                energy: Math.random() * 100,
                water: Math.random() * 100
            },
            hasAtmosphere: options.hasAtmosphere !== undefined ? options.hasAtmosphere : true,
            distanceFromSun: options.distanceFromSun || 100,
            seed: options.seed || Math.random() * 1000,
            hasOcean: options.hasOcean !== undefined ? options.hasOcean : true,
            oceanLevel: options.oceanLevel || 0.65
        };

        // Calculate climate type based on distance from sun
        this.determineClimateType();

        // Create container for the planet
        this.container = new THREE.Object3D();
        this.container.userData = {
            isSelectable: true,
            isPlanet: true,
            planetData: {
                name: this.options.name,
                type: this.options.type,
                climate: this.options.climate,
                resources: this.options.resources,
                structures: [] // Will store orbital structures
            }
        };

        // Create the planet
        this.createPlanet();
        
        // Add atmosphere if needed
        if (this.options.hasAtmosphere) {
            this.createAtmosphere();
        }
        
        // Container for orbital structures
        this.orbitalsContainer = new THREE.Object3D();
        this.container.add(this.orbitalsContainer);

        // Add animate method to the container
        this.container.animate = (delta) => this.animate(delta);
        
        // Add building method to the container
        this.container.addStructure = (type) => this.addStructure(type);
        
        return this.container;
    }

    determineClimateType() {
        // Determine climate zone based on distance from sun
        const distance = this.options.distanceFromSun;
        
        if (distance <= 50) {
            this.options.climate = 'desert';
        } else if (distance <= 100) {
            this.options.climate = 'tropical';
        } else if (distance <= 200) {
            this.options.climate = 'temperate';
        } else if (distance <= 300) {
            this.options.climate = 'arctic';
        } else {
            this.options.climate = 'ice';
        }
        
        console.log(`Planet at distance ${distance} has climate: ${this.options.climate}`);
        
        // Adjust planet features based on climate
        if (this.options.climate === 'desert') {
            this.options.hasOcean = false;
            this.options.oceanLevel = 0;
            this.options.hasAtmosphere = this.options.hasAtmosphere && Math.random() > 0.3; // 30% chance of no atmosphere
        } else if (this.options.climate === 'ice') {
            this.options.oceanLevel = 0.4; // Less water, more ice
        }
    }

    createPlanet() {
        // Create geometry with higher detail
        const geometry = new THREE.SphereGeometry(
            1, // Base radius of 1 (will be scaled by shader)
            Math.max(64, this.options.detail), // Higher resolution
            Math.max(64, this.options.detail)
        );
        geometry.computeTangents(); // Needed for normal mapping

        // Set up planet parameters based on climate
        const planetParams = this.getPlanetParamsForClimate();
        
        // Set light direction based on position from sun (assuming sun at origin)
        // Calculate direction from planet position to origin (sun)
        const dirToSun = new THREE.Vector3();
        
        // Since we don't know the planet's final position yet (it will be placed in orbit),
        // use the distanceFromSun to approximate direction
        // This is simplified - in reality we would need the actual planet position relative to the sun
        const angle = Math.random() * Math.PI * 2; // Random angle around sun
        dirToSun.set(
            -Math.cos(angle), // X component pointing toward sun
            0.5, // Slight Y angle for better lighting
            -Math.sin(angle)  // Z component pointing toward sun
        );
        
        planetParams.lightDirection.value.copy(dirToSun.normalize());

        const material = new THREE.ShaderMaterial({
            uniforms: planetParams,
            vertexShader: this.getVertexShader(),
            fragmentShader: this.getFragmentShader(),
        });

        // Create main planet mesh
        this.planetMesh = new THREE.Mesh(geometry, material);
        this.planetMesh.scale.set(
            this.options.radius,
            this.options.radius,
            this.options.radius
        );
        this.planetMesh.castShadow = true;
        this.planetMesh.receiveShadow = true;
        this.container.add(this.planetMesh);

        // Apply random rotation
        this.planetMesh.rotation.x = Math.random() * Math.PI;
        this.planetMesh.rotation.y = Math.random() * Math.PI;
        this.planetMesh.rotation.z = Math.random() * Math.PI;
    }

    getPlanetParamsForClimate() {
        // Define base planet parameters that work for procedural generation
        const params = {
            type: { value: 2 }, // Ridged noise for mountains
            radius: { value: 1.0 }, // Base radius of 1 (scaled by mesh)
            amplitude: { value: 0.1 }, // Height of terrain features
            sharpness: { value: 2.0 }, // Sharpness of features
            offset: { value: 0.0 }, // Offset of terrain
            period: { value: 0.8 }, // Period of noise
            persistence: { value: 0.5 }, // How much each octave contributes
            lacunarity: { value: 2.0 }, // How frequency increases per octave
            octaves: { value: 8 }, // Number of noise layers
            undulation: { value: 0.0 }, // Additional wave pattern
            ambientIntensity: { value: 0.3 }, // Ambient light - increased
            diffuseIntensity: { value: 1.2 }, // Diffuse light - increased
            specularIntensity: { value: 0.7 }, // Specular highlights - increased
            shininess: { value: 10.0 }, // Surface shininess
            lightDirection: { value: new THREE.Vector3(-1, 0.5, 1).normalize() }, // Light direction - adjusted
            lightColor: { value: new THREE.Color(0xffffff) }, // Light color
            bumpStrength: { value: 0.5 }, // Bump mapping strength
            bumpOffset: { value: 0.0 }, // Bump mapping offset
            // Default terrain colors
            color1: { value: new THREE.Color(0.1, 0.1, 0.6) }, // Deep color (water or lowlands)
            color2: { value: new THREE.Color(0.1, 0.4, 0.2) }, // Intermediate color
            color3: { value: new THREE.Color(0.4, 0.4, 0.2) }, // High color
            color4: { value: new THREE.Color(0.6, 0.6, 0.6) }, // Mountain color
            color5: { value: new THREE.Color(1.0, 1.0, 1.0) }, // Peak color (snow)
            transition2: { value: 0.1 }, // Transition height to color2
            transition3: { value: 0.25 }, // Transition height to color3
            transition4: { value: 0.5 }, // Transition height to color4
            transition5: { value: 0.7 }, // Transition height to color5
            blend12: { value: 0.05 }, // Blend width for transition 1-2
            blend23: { value: 0.05 }, // Blend width for transition 2-3
            blend34: { value: 0.05 }, // Blend width for transition 3-4
            blend45: { value: 0.05 }  // Blend width for transition 4-5
        };

        // Adjust parameters based on climate type
        switch (this.options.climate) {
            case 'desert':
                // Desert planets: reddish, less water, more rugged terrain
                params.type.value = 2; // Ridged noise for dunes and mountains
                params.amplitude.value = 0.15; // Higher amplitude for more dramatic terrain
                params.sharpness.value = 3.0; // Sharper features
                params.color1.value = new THREE.Color(0.6, 0.4, 0.2); // Sandy base
                params.color2.value = new THREE.Color(0.7, 0.5, 0.3); // Mid-level sand
                params.color3.value = new THREE.Color(0.55, 0.35, 0.2); // Rocky areas
                params.color4.value = new THREE.Color(0.4, 0.3, 0.2); // Mountain rock
                params.color5.value = new THREE.Color(0.3, 0.2, 0.1); // Mountain peaks
                params.transition2.value = 0.1;
                params.transition3.value = 0.3;
                params.transition4.value = 0.5;
                params.transition5.value = 0.7;
                break;
                
            case 'tropical':
                // Tropical planets: green, blue water
                params.type.value = 2; // Ridged noise for varied terrain
                params.amplitude.value = 0.12;
                params.sharpness.value = 2.2;
                params.color1.value = new THREE.Color(0.0, 0.2, 0.5); // Deep blue water
                params.color2.value = new THREE.Color(0.8, 0.7, 0.5); // Beach sand
                params.color3.value = new THREE.Color(0.1, 0.6, 0.2); // Lush greens
                params.color4.value = new THREE.Color(0.4, 0.4, 0.2); // Mountain base
                params.color5.value = new THREE.Color(0.8, 0.8, 0.8); // Mountain peaks
                params.transition2.value = this.options.oceanLevel;
                params.transition3.value = this.options.oceanLevel + 0.05;
                params.transition4.value = 0.6;
                params.transition5.value = 0.75;
                break;
                
            case 'temperate':
                // Temperate planets: blues, greens, snow caps
                params.type.value = 2; // Ridged noise for terrain
                params.amplitude.value = 0.12;
                params.color1.value = new THREE.Color(0.1, 0.2, 0.5); // Deep blue water
                params.color2.value = new THREE.Color(0.7, 0.7, 0.5); // Beach/shore
                params.color3.value = new THREE.Color(0.2, 0.4, 0.1); // Forests/grasslands
                params.color4.value = new THREE.Color(0.5, 0.4, 0.3); // Mountains
                params.color5.value = new THREE.Color(0.9, 0.9, 0.9); // Snow peaks
                params.transition2.value = this.options.oceanLevel;
                params.transition3.value = this.options.oceanLevel + 0.04;
                params.transition4.value = 0.5;
                params.transition5.value = 0.65;
                break;
                
            case 'arctic':
                // Arctic planets: whites, blues, some vegetation
                params.type.value = 2; // Ridged noise for terrain
                params.amplitude.value = 0.1;
                params.color1.value = new THREE.Color(0.1, 0.2, 0.4); // Deep blue water
                params.color2.value = new THREE.Color(0.6, 0.6, 0.7); // Ice coastline
                params.color3.value = new THREE.Color(0.4, 0.5, 0.4); // Sparse vegetation
                params.color4.value = new THREE.Color(0.7, 0.7, 0.7); // Rocky mountains
                params.color5.value = new THREE.Color(0.95, 0.95, 0.95); // Snow
                params.transition2.value = this.options.oceanLevel;
                params.transition3.value = this.options.oceanLevel + 0.03;
                params.transition4.value = 0.4;
                params.transition5.value = 0.55;
                break;
                
            case 'ice':
                // Ice planets: mostly whites and pale blues
                params.type.value = 2; // Ridged noise for terrain
                params.amplitude.value = 0.08; // Less dramatic terrain
                params.color1.value = new THREE.Color(0.2, 0.3, 0.5); // Deep blue ice water
                params.color2.value = new THREE.Color(0.7, 0.8, 0.9); // Light blue ice
                params.color3.value = new THREE.Color(0.8, 0.85, 0.9); // Pale blue snow
                params.color4.value = new THREE.Color(0.9, 0.9, 0.95); // Snow
                params.color5.value = new THREE.Color(1.0, 1.0, 1.0); // Pure snow
                params.transition2.value = this.options.oceanLevel;
                params.transition3.value = this.options.oceanLevel + 0.02;
                params.transition4.value = 0.3;
                params.transition5.value = 0.5;
                break;
                
            default:
                // Default temperate planet colors and parameters
                params.transition2.value = this.options.oceanLevel;
                params.transition3.value = this.options.oceanLevel + 0.01;
                break;
        }

        // If no ocean is present, adjust the transitions
        if (!this.options.hasOcean) {
            // Start with lowest terrain color
            params.color1.value = params.color3.value;
            // Move all transitions down
            params.transition2.value = 0.05;
            params.transition3.value = 0.25;
            params.transition4.value = 0.5;
        }

        return params;
    }

    getVertexShader() {
        return `
        ${noiseShader}
        
        uniform int type;
        uniform float radius;
        uniform float amplitude;
        uniform float sharpness;
        uniform float offset;
        uniform float period;
        uniform float persistence;
        uniform float lacunarity;
        uniform int octaves;
        uniform float undulation;
        uniform float bumpStrength;
        uniform float bumpOffset;
        
        varying vec3 vNormal;
        varying vec3 vPosition;
        varying float vDisplacement;
        varying vec2 vUv;
        
        void main() {
            vUv = uv;
            vec3 transformed = position;
            vNormal = normalize(normal);
            
            float pX = vNormal.x * period;
            float pY = vNormal.y * period;
            float pZ = vNormal.z * period;
            
            float displacement = 0.0;
            
            if (type == 1) {
                // FBM Fractal Noise
                displacement = fbm(vec3(pX, pY, pZ), octaves, persistence, lacunarity);
                displacement = pow(abs(displacement), sharpness) * amplitude + offset;
            } 
            else if (type == 2) {
                // Ridged Multifractal Noise (better for mountains)
                float signal = fbm(vec3(pX, pY, pZ), octaves, persistence, lacunarity);
                signal = 1.0 - abs(signal);
                signal = pow(signal, sharpness);
                displacement = signal * amplitude + offset;
            }
            else if (type == 3) {
                // Swiss Turbulence
                float signal = 1.0 - abs(fbm(vec3(pX, pY, pZ), octaves, persistence, lacunarity));
                signal = signal * signal;
                displacement = signal * amplitude + offset;
            }
            else if (type == 4) {
                // Billowy noise - good for clouds
                float signal = abs(fbm(vec3(pX, pY, pZ), octaves, persistence, lacunarity));
                displacement = signal * amplitude + offset;
            }
            else {
                // Type 0 or any other - smooth basic noise
                displacement = fbm(vec3(pX, pY, pZ), octaves, persistence, lacunarity) * amplitude + offset;
            }
            
            // Add undulation for additional interest (optional wave pattern)
            if (undulation > 0.0) {
                float wave = sin(vNormal.y * 10.0) * cos(vNormal.x * 10.0) * undulation;
                displacement += wave;
            }
            
            // Apply displacement along the vertex normal
            transformed += vNormal * displacement;
            
            // Save the displacement value for use in the fragment shader
            vDisplacement = displacement;
            
            // Pass the position
            vPosition = transformed;
            
            gl_Position = projectionMatrix * modelViewMatrix * vec4(transformed, 1.0);
        }
        `;
    }

    getFragmentShader() {
        return `
        uniform float ambientIntensity;
        uniform float diffuseIntensity;
        uniform float specularIntensity;
        uniform float shininess;
        uniform vec3 lightDirection;
        uniform vec3 lightColor;
        uniform float bumpStrength;
        uniform float bumpOffset;
        
        uniform vec3 color1;
        uniform vec3 color2;
        uniform vec3 color3;
        uniform vec3 color4;
        uniform vec3 color5;
        uniform float transition2;
        uniform float transition3;
        uniform float transition4;
        uniform float transition5;
        uniform float blend12;
        uniform float blend23;
        uniform float blend34;
        uniform float blend45;
        
        varying vec3 vNormal;
        varying vec3 vPosition;
        varying float vDisplacement;
        varying vec2 vUv;
        
        // Function to blend between two colors using a smooth step
        vec3 smoothBlend(vec3 color1, vec3 color2, float threshold, float smoothness, float value) {
            float t = smoothstep(threshold - smoothness, threshold + smoothness, value);
            return mix(color1, color2, t);
        }
        
        // Bump mapping function for normal perturbation
        vec3 perturbNormal(vec3 normal, float displacement) {
            // This is a simple normal perturbation based on the displacement
            // For more accurate results, we'd calculate actual derivatives
            float bump = bumpStrength * displacement + bumpOffset;
            vec3 bumpedNormal = normal + vec3(cos(bump * 20.0) * 0.01, 
                                             sin(bump * 20.0) * 0.01, 
                                             cos(bump * 15.0) * 0.01);
            return normalize(bumpedNormal);
        }
        
        void main() {
            // Normalize vectors for lighting
            vec3 normal = normalize(vNormal);
            vec3 light = normalize(lightDirection);
            
            // Apply simple bump mapping based on displacement
            normal = perturbNormal(normal, vDisplacement);
            
            // Lighting calculations
            float nDotL = max(dot(normal, light), 0.0);
            
            // Ambient
            vec3 ambient = lightColor * ambientIntensity;
            
            // Diffuse
            vec3 diffuse = lightColor * diffuseIntensity * nDotL;
            
            // Specular (Blinn-Phong)
            vec3 viewDir = normalize(-vPosition); // assuming camera at origin
            vec3 halfDir = normalize(light + viewDir);
            float specAngle = max(dot(normal, halfDir), 0.0);
            float specular = pow(specAngle, shininess) * specularIntensity;
            
            // Color based on displacement value
            vec3 terrainColor;
            
            // Multi-color terrain with smooth transitions
            if (vDisplacement < transition2) {
                terrainColor = smoothBlend(color1, color2, transition2, blend12, vDisplacement);
            } 
            else if (vDisplacement < transition3) {
                terrainColor = smoothBlend(color2, color3, transition3, blend23, vDisplacement);
            }
            else if (vDisplacement < transition4) {
                terrainColor = smoothBlend(color3, color4, transition4, blend34, vDisplacement);
            }
            else {
                terrainColor = smoothBlend(color4, color5, transition5, blend45, vDisplacement);
            }
            
            // Combine lighting with terrain color
            vec3 finalColor = terrainColor * (ambient + diffuse) + lightColor * specular;
            
            // Output final color
            gl_FragColor = vec4(finalColor, 1.0);
        }
        `;
    }

    createAtmosphere() {
        // Set atmosphere parameters based on climate
        const atmosphereParams = this.getAtmosphereParamsForClimate();
        
        // Create atmosphere
        const atmosphere = new Atmosphere(atmosphereParams);
        this.atmosphere = atmosphere;
        this.container.add(atmosphere);
    }

    getAtmosphereParamsForClimate() {
        // Base atmosphere parameters
        const params = {
            particles: 4000,
            minParticleSize: 50,
            maxParticleSize: 100,
            radius: this.options.radius + 0.5,
            thickness: 1.5,
            density: 0.2,
            opacity: 0.35,
            scale: 8,
            color: new THREE.Color(0xffffff),
            speed: 0.03,
            lightDirection: { value: new THREE.Vector3(1, 1, 1).normalize() }
        };

        // Customize atmosphere based on climate
        switch (this.options.climate) {
            case 'desert':
                // Thin, reddish atmosphere for desert planets
                params.particles = 2000;
                params.color = new THREE.Color(0xff8866);
                params.thickness = 1.1;
                params.opacity = 0.2;
                params.density = 0.1;
                break;
            case 'tropical':
                // Thick, blue-green atmosphere
                params.particles = 6000;
                params.color = new THREE.Color(0x66ffee);
                params.thickness = 1.7;
                params.opacity = 0.4;
                params.density = 0.3;
                break;
            case 'temperate':
                // Blue atmosphere, earth-like
                params.particles = 4000;
                params.color = new THREE.Color(0x88aaff);
                params.thickness = 1.5;
                params.opacity = 0.35;
                params.density = 0.25;
                break;
            case 'arctic':
                // Pale blue, thinner atmosphere
                params.particles = 3000;
                params.color = new THREE.Color(0xaaccff);
                params.thickness = 1.3;
                params.opacity = 0.3;
                params.density = 0.2;
                break;
            case 'ice':
                // Very pale, thin atmosphere
                params.particles = 2000;
                params.color = new THREE.Color(0xccddff);
                params.thickness = 1.2;
                params.opacity = 0.25;
                params.density = 0.15;
                break;
        }

        // If a custom atmosphere color was specified, use it
        if (this.options.atmosphereColor) {
            params.color = this.options.atmosphereColor;
        }

        return params;
    }

    addStructure(type) {
        // Create orbital structure based on type
        let structure;
        let orbitRadius = this.options.radius * 1.8; // Base orbit radius
        let orbitSpeed = 0.00005; // Very slow orbit speed
        let color, scale;
        
        // Configure structure based on type
        switch(type) {
            case 'mining':
                color = 0xffcc00; // Gold color for mining
                scale = 0.15;
                orbitRadius = this.options.radius * 1.6;
                break;
            case 'research':
                color = 0x00ccff; // Blue for research
                scale = 0.2;
                orbitRadius = this.options.radius * 2.0;
                break;
            case 'colony':
                color = 0x00ff44; // Green for colony
                scale = 0.25;
                orbitRadius = this.options.radius * 2.4;
                break;
            case 'defense':
                color = 0xff3300; // Red for defense
                scale = 0.18;
                orbitRadius = this.options.radius * 1.8;
                break;
            default:
                color = 0xffffff;
                scale = 0.15;
        }
        
        // Create simple cube structure for now
        const geometry = new THREE.BoxGeometry(
            this.options.radius * scale,
            this.options.radius * scale,
            this.options.radius * scale
        );
        
        const material = new THREE.MeshStandardMaterial({
            color: color,
            metalness: 0.8,
            roughness: 0.2,
            emissive: color,
            emissiveIntensity: 0.2
        });
        
        structure = new THREE.Mesh(geometry, material);
        structure.castShadow = true;
        structure.receiveShadow = true;
        
        // Create orbit container
        const orbitContainer = new THREE.Object3D();
        orbitContainer.add(structure);
        
        // Position structure at orbit distance
        structure.position.x = orbitRadius;
        
        // Randomize initial position in orbit
        orbitContainer.rotation.y = Math.random() * Math.PI * 2;
        orbitContainer.rotation.x = (Math.random() - 0.5) * 0.3; // Slight tilt to orbit
        
        // Store orbital data
        orbitContainer.userData = {
            type: type,
            orbitSpeed: orbitSpeed
        };
        
        // Add to orbitals container
        this.orbitalsContainer.add(orbitContainer);
        
        // Add to planet data for saving
        this.container.userData.planetData.structures.push({
            type: type,
            orbitRadius: orbitRadius,
            orbitAngle: orbitContainer.rotation.y,
            orbitTilt: orbitContainer.rotation.x,
            scale: scale,
            color: color
        });
        
        return orbitContainer;
    }

    animate(delta) {
        // Rotate planet - 100x slower rotation
        if (this.planetMesh) {
            this.planetMesh.rotation.y += this.options.rotationSpeed * delta * 0.01;
        }
        
        // Animate atmosphere if present - 100x slower rotation
        if (this.atmosphere) {
            this.atmosphere.rotation.y += this.options.cloudSpeed * 0.005 * delta; // reduced by 100x
            this.atmosphere.update(performance.now() * 0.001);
        }
        
        // Animate orbital structures - keeping orbital speed the same
        if (this.orbitalsContainer) {
            this.orbitalsContainer.children.forEach(orbit => {
                orbit.rotation.y += orbit.userData.orbitSpeed * delta;
            });
        }
    }
}